.sliding-image-container {
    width: 100%;
    /* padding: 10px; */
    /* margin: 10px 0; */
    /* background-color: white; */
  }

  .carousel-container {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
  }

  table {
    width: 100%;
    border-collapse: collapse;
  }

  .image-cell, .text-cell {
    /* padding: 10px; */
    vertical-align: middle;
  }

  .image-cell {
    width: 50%; /* Adjust the width as needed */
  }

  .text-cell {
    width: 45%; /* Adjust the width as needed */
    text-align: left;
    justify-content: center;
    padding: 10%;
  }

  .product-image {
    max-width: 100%;
    height: 40%;
    /* border-radius: 10px; */
  }

  .text-cell h1 {
    margin-bottom: 10px;
    text-size-adjust: 60px;
  }

  .text-cell p {
    margin: 0%;
    padding-top: 5%;
  }

  .slick-slide img {
    width: 100%; /* Adjust to ensure the image fills the container */
    height: 40%;
    text-size-adjust: 50px;
  }
